.home-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: row;
}

.logo-section {
  background-color: var(--color-white);
  width: 50vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.logo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: auto;
}

.logo-image {
  width: 50%;
  height: auto;
}

.logo-subtext {
  margin-top: 40px;
  color: var(--color-gray);
  font-size: 1.75vw;
  font-weight: 400;
  text-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.1);
  text-align: center;
  cursor: default;
}

.login-section {
  width: 50vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.login-form {
  width: 100%;
  height: 50%;
  margin-top: 25vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.title {
  color: var(--color-white);
  font-size: 7.5vw;
  font-weight: 500;
  text-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.1);
  text-align: center;
  line-height: 1;
  cursor: default;
}

.login-input {
  margin-top: 25px;
  border: none;
  outline: none;
  padding: 1vw 1vw;
  border-radius: 0.40vw;
  box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.15);
  width: 71%;
  color: var(--color-black);
  font-size: 1vw;
  font-weight: 400;
  text-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.1);
}

.login-input::placeholder {
  color: var(--color-gray);
  font-size: 1vw;
  font-weight: 400;
  text-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.1);
}

.login-buttons {
  margin-top: 25px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 71%;
}

.join-button {
  border: none;
  outline: none;
  cursor: pointer;
  width: 45%;
  padding: 1vw 1vw;
  border-radius: 0.40vw;
  box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.15);
  background-color: var(--color-pink);
  color: var(--color-white);
  font-size: 1vw;
  font-weight: 700;
  text-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.1);
}

.join-button:hover {
  transform: translateY(-0.25vh);
}

.create-button {
  border: none;
  outline: none;
  cursor: pointer;
  width: 45%;
  padding: 0.9vw 1vw;
  border-radius: 0.40vw;
  box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.15);
  background-color: var(--color-aqua);
  color: var(--color-white);
  font-size: 1vw;
  font-weight: 700;
  text-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.1);
}

.create-button:hover {
  transform: translateY(-0.25vh);
}

.footer {
  width: 75%;
  height: auto;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer-creators {
  color: var(--color-white);
  font-size: 1vw;
  font-weight: 600;
  text-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.1);
  text-align: center;
  cursor: default;
}

.creator-name {
  color: var(--color-white);
  text-decoration: none;
  font-size: 1vw;
  font-weight: 600;
}

.creator-name:hover {
  border-bottom: 0.35vh solid var(--color-white);
}
