.playerContainer {
  width: 100%;
  height: 100%;
  background-color: var(--color-white);
  /* border: 0.3vw solid var(--color-white); */
  border-radius: 0.6vw;
  box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.15);
  position: relative;
  padding: 2%;
  overflow: hidden;
  display: grid;
  justify-items: center;
  align-items: center;
}

.actor-overlay {
  position: absolute;
  width: 100%;
  height: 100%;

  background-color: #ffffffb3;

  z-index: 30;
  border-radius: 0.6vw;

  display: grid;
  justify-items: center;
  align-items: center;
}

.videoArea {
  width: 100%;
  height: 100%;
  position: relative;
  background-color: var(--color-black);
  border-radius: 0.6vw;
}

.playerVideo {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 0.6vw;
  z-index: 10;
  position: relative;
}

.timer {
  height: 200%;
  width: 200%;
  border-radius: 50%;
  background: -webkit-gradient(
    linear,
    left top, right top,
    color-stop(50%, var(--color-pink)),
    color-stop(50%, var(--color-white))
  );
  background: -webkit-linear-gradient(
    left,
    var(--color-pink) 50%,
    var(--color-white) 50%
  );
  background: linear-gradient(
    90deg,
    var(--color-pink) 50%,
    var(--color-white) 50%
  );
}

.mask {
  height: 200%;
  width: 100%;
  margin-left: -100%;
  content: "";
  border-radius: 100% 0 0 100% / 50% 0 0 50%;
  -webkit-transform-origin: 100% 50%;
          transform-origin: 100% 50%;
  -webkit-transition-delay: 1s;
          transition-delay: 1s;
  background-color: var(--color-white);
}

.mask,
.timer,
.videoArea {
  grid-column: 1;
  grid-row: 1;
}

.volumeControls {
  position: absolute;
  bottom: 0;
  right: 0;
  display: flex;
  flex-direction: column-reverse;
  justify-content: flex-start;
  align-items: center;
  height: 40%;
  width: 10%;
  margin-bottom: 3%;
  margin-right: 1.5%;
  z-index: 20;
}

.volumeButton {
  opacity: 0;
  width: 60%;
  height: auto;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
  -webkit-transition: all 0s ease 0.2s;
  transition: all 0s ease 0.2s;
}

.slider {
  visibility: visible;
  opacity: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  overflow: hidden;
  height: 5%;
  width: 200%;
  border-radius: 3px;
  margin-bottom: 110%;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
  -webkit-transition: all 0s ease 0.2s;
  transition: all 0s ease 0.2s;
  -webkit-transform: translateY(0%) rotate(270deg);
          transform: translateY(0%) rotate(270deg);
}

.slider::-webkit-slider-runnable-track {
  background: var(--color-white);
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 0;
  height: 0;
  background: var(--color-white);
  box-shadow: -100vw 0 0 100vw var(--color-pink);
}

.videoArea:hover > .volumeControls .volumeButton {
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
  opacity: 100;
}

.volumeButton:hover {
  cursor: pointer;
}

.slider:hover {
  cursor: pointer;
  visibility: visible;
  opacity: 100;
}

.volumeButton:hover + .slider {
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
  visibility: visible;
  opacity: 100;
}

